header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

.header__links {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__links::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}



.picture {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 5rem;
    border-radius: 0 0 12rem 12rem;
    overflow: hidden;
    padding: 5rem 1.5rem 0rem 1.5rem;
}

.picture, img {
    scale: 125%;
}

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 200;
    font-size: 0.9rem;
}


/*Responsive, Tablet størrelse */

@media screen and (max-width: 1024px){
    header {
        height: 68vh;
    }
}

/*Responsive, Mobil størrelse */

@media screen and (max-width: 600px){
    header {
        height: 100vh;
    }
    .scroll__down, .header__links {
        display: none;
    }

}