footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}

footer a {
    color: var(--color-bg);

}

.footer__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;

}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials {

    transform: scale(200%);
    display: flex;
    justify-content: center;
    gap: 2rem;
    margin-bottom: 4rem;
    border-radius: 0.4rem;
    padding: 1rem 0 1rem;
    border: 2px solid var(--color-bg);
}

.footer__socials a:hover {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border: 1px solid transparent;
}
